import React, { Component } from 'react';
import { changeCurrentDescFontSelected } from '../actions';
import { BODY_960, BODY_600 } from '../constants/defaults';
import { connect } from 'react-redux';
import { scaleLinear } from 'd3';
import { FONTS } from '../constants/defaults';
import _ from 'lodash';

class DescriptionKo extends Component {

  handleCurrentDescFontSelected(name, e) {
    e.stopPropagation();
    this.props.dispatch(changeCurrentDescFontSelected(name));
  }

  retrieveFontName(currentDescFont) {
    var result = {};

    _.each(currentDescFont, (v, k) => {
      let font = _.find(FONTS, fontData => { return v == fontData.id });
      
      result[k] = {
        fontFamily: font.fontName
      };

    });

    return result;
  }

  render() {
    let { currentDescFontSelected, currentDescFont, screenWidth } = this.props;
    let marginRightScale = scaleLinear().domain([960, 1280]).clamp(true).range([20, 150]);
    let selectedOrNot = (areaName) => {
      return areaName == currentDescFontSelected ? "marching-ants" : "";
    }

    let fontNames = this.retrieveFontName(currentDescFont);

    return (
      <div className="description__container">
        <a href="javascript:void(0);" onClick={this.handleCurrentDescFontSelected.bind(this, "title")}>
          <h4 className={selectedOrNot("title")} style={fontNames.title}>
             +  
          </h4>
        </a>

        <a href="javascript:void(0);" onClick={this.handleCurrentDescFontSelected.bind(this, "big")}>
          <p className={`description__big ${selectedOrNot('big')}`} style={fontNames.big}>
             +                  .
          </p>
        </a>

        <a href="javascript:void(0);" onClick={this.handleCurrentDescFontSelected.bind(this, "paragraph")}>
          <p style={fontNames.paragraph} className={selectedOrNot('paragraph')}>
                  , ,       .      65,000   ,   2,000       30 .                    .
          </p>
        </a>

        <a href="javascript:void(0);" onClick={this.handleCurrentDescFontSelected.bind(this, "paragraph")}>
          <p style={fontNames.paragraph} className={selectedOrNot('paragraph')}>
             +      ,              .       ,          x   . 
          </p>
        </a>

        <a href="javascript:void(0);" onClick={this.handleCurrentDescFontSelected.bind(this, "paragraph")}>
          <p style={fontNames.paragraph} className={selectedOrNot('paragraph')}>
                   , ,       .      ,             ,        . 
          </p>
        </a>

        <a href="javascript:void(0);" onClick={this.handleCurrentDescFontSelected.bind(this, "paragraph")}>
          <p style={fontNames.paragraph} className={selectedOrNot('paragraph')}>
                     .  +      ,                 . 
          </p>
        </a>


        <div className="l-apple-box--double"></div>

        <a href="javascript:void(0);" onClick={this.handleCurrentDescFontSelected.bind(this, "title")}>
          <h4 style={fontNames.title} className={selectedOrNot('title')}>
             
          </h4>
        </a>

        <a href="javascript:void(0);" onClick={this.handleCurrentDescFontSelected.bind(this, "paragraph")}>
          <p style={fontNames.paragraph} className={selectedOrNot('paragraph')}>
                  19  21   .    , ,        ,     . 1446               ,            .
          </p>
        </a>

        <a href="javascript:void(0);" onClick={this.handleCurrentDescFontSelected.bind(this, "paragraph")}>
          <p style={fontNames.paragraph} className={selectedOrNot('paragraph')}>
                       (ligature) ,            ,    .        XXXX    ,        ,              . 
          </p>
        </a>

        <a href="javascript:void(0);" onClick={this.handleCurrentDescFontSelected.bind(this, "paragraph")}>
          <p style={fontNames.paragraph} className={selectedOrNot('paragraph')}>
                       (CJK)       ,       . @font-face             , xxx       . 
          </p>
        </a>

        <a href="javascript:void(0);" onClick={this.handleCurrentDescFontSelected.bind(this, "paragraph")}>
          <p style={fontNames.paragraph} className={selectedOrNot('paragraph')}>
               cross-site caching      ,                .   CJK             . 
          </p>
        </a>

        <a href="javascript:void(0);" onClick={this.handleCurrentDescFontSelected.bind(this, "title")}>
          <h4 style={fontNames.title} className={selectedOrNot('title')}>
               
          </h4>
        </a>

        <div className="description__column-container">
          {
            screenWidth <= BODY_960 ? 
            <div className="description__column-right">
              <div className="description__stat-wrap">
                <a href="javascript:void(0);" onClick={this.handleCurrentDescFontSelected.bind(this, "big")}>
                  <h5 className={`description__big-numeric ${selectedOrNot('big')}`} style={fontNames.big}>
                    65,535
                  </h5>
                </a>
                <a href="javascript:void(0);" onClick={this.handleCurrentDescFontSelected.bind(this, "paragraph")}>
                  <p className={`right ${selectedOrNot('paragraph')}`} style={fontNames.paragraph}>
                       ()<br/>
                    Noto Sans CJK KR
                  </p>
                </a>
              </div>

              <div className="description__stat-wrap">
                <a href="javascript:void(0);" onClick={this.handleCurrentDescFontSelected.bind(this, "big")}>
                  <h5 className={`description__big-numeric ${selectedOrNot('big')}`} style={fontNames.big}>
                    2,416
                  </h5>
                </a>
                <a href="javascript:void(0);" onClick={this.handleCurrentDescFontSelected.bind(this, "paragraph")}>
                  <p className={`right ${selectedOrNot('paragraph')}`} style={fontNames.paragraph}>
                       <br/>( +  + )<br/>
                    Noto Sans
                  </p>
                </a>

              </div>
            </div> : null
          }
          <div className="description__column-left" style={{ marginRight: marginRightScale(screenWidth) }}>
            <a href="javascript:void(0);" onClick={this.handleCurrentDescFontSelected.bind(this, "paragraph")}>
              <p style={fontNames.paragraph} className={selectedOrNot('paragraph')}>
                Since 2015, the Google Fonts API has been available in mainland China, but Chinese users face a problem using webfonts in their own language: While Latin webfonts are typically 100400KB and other writing systems usually range between 200600KB, the Noto Sans SC (Simplified Chinese) font file is much larger (around 19MB) because it includes 44,683 characters.
              </p>
            </a>

            <a href="javascript:void(0);" onClick={this.handleCurrentDescFontSelected.bind(this, "paragraph")}>
              <p style={fontNames.paragraph} className={selectedOrNot('paragraph')}>
                The experience for Chinese webfont users can be improved if the file size and webfont latency are reduced: In any large corpus of documents, there are patterns in character usage. By scanning webpages in Chinese, we model which characters are most likely to appear together, which informs a static subset slicing strategy that sorts all 44,683 glyphs into 102 slices.
              </p>
            </a>

            <a href="javascript:void(0);" onClick={this.handleCurrentDescFontSelected.bind(this, "paragraph")}>
              <p style={fontNames.paragraph} className={selectedOrNot('paragraph')}>
                Using this method, the entire web page will seem to be using Noto Sans SC while only loading the set of font slices required for that page, which we expect to be much faster than loading the whole font. Cross-site caching is a key benefit of using the Google Fonts API, and since each slice of this font can be used across websites, the latency benefits increase over time.
              </p>
            </a>

            <a href="javascript:void(0);" onClick={this.handleCurrentDescFontSelected.bind(this, "paragraph")}>
              <p style={fontNames.paragraph} className={selectedOrNot('paragraph')}>
                For more information about Noto, check out the other parts of Noto available on this page, and visit google.com/get/noto. Another version of the 'Noto Sans SC' family is also available on this page, which does not support the slicing optimization but includes more styles.
              </p>
            </a>
          </div>

          {
            screenWidth > BODY_960 ? 
            <div className="description__column-right">
              <div className="description__stat-wrap">
                <a href="javascript:void(0);" onClick={this.handleCurrentDescFontSelected.bind(this, "big")}>
                  <h5 className={`description__big-numeric ${selectedOrNot('big')}`} style={fontNames.big}>
                    65,535
                  </h5>
                </a>
                <a href="javascript:void(0);" onClick={this.handleCurrentDescFontSelected.bind(this, "paragraph")}>
                  <p className={`right ${selectedOrNot('paragraph')}`} style={fontNames.paragraph}>
                       ()<br/>
                    Noto Sans CJK KR
                  </p>
                </a>
              </div>

              <div className="description__stat-wrap">
                <a href="javascript:void(0);" onClick={this.handleCurrentDescFontSelected.bind(this, "big")}>
                  <h5 className={`description__big-numeric ${selectedOrNot('big')}`} style={fontNames.big}>
                    2,416
                  </h5>
                </a>
                <a href="javascript:void(0);" onClick={this.handleCurrentDescFontSelected.bind(this, "paragraph")}>
                  <p className={`right ${selectedOrNot('paragraph')}`} style={fontNames.paragraph}>
                       <br/>( +  + )<br/>
                    Noto Sans
                  </p>
                </a>

              </div>
            </div> : null
          }

        </div>

        <a href="javascript:void(0);" onClick={this.handleCurrentDescFontSelected.bind(this, "title")}>
          <h4 style={fontNames.title} className={selectedOrNot('title')}>
            SIL    (OFL)
          </h4>
        </a>

        <a href="javascript:void(0);" onClick={this.handleCurrentDescFontSelected.bind(this, "paragraph")}>
          <p style={fontNames.paragraph} className={selectedOrNot('paragraph')}>
                   SIL  SIL      ,            .        OFL          ,      .         , ,     . 
          </p>
        </a>

        <a href="javascript:void(0);" onClick={this.handleCurrentDescFontSelected.bind(this, "paragraph")}>
          <p style={fontNames.paragraph} className={selectedOrNot('paragraph')}>
             +        ,      ,       OFL  . 
          </p>
        </a>

      </div>
    );
  }
}

let mapStateToProps = state => {
  return {
    screenWidth: state.screenWidth,
    currentDescFont: state.currentDescFont,
    locale: state.locale,
    currentDescFontSelected: state.currentDescFontSelected
  }
};

export default connect(mapStateToProps)(DescriptionKo);



